/* Overlay closing cross */
.overlay-close {
  position: absolute;
  display: inline-block;
  width: 50px;
  height: 50px;
  overflow: hidden;
  right: 10px;
  top: 10px;
  cursor: pointer; }
  .overlay-close:hover::before, .overlay-close:hover::after {
    background: #919193; }
  .overlay-close::before, .overlay-close::after {
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #000; }
  .overlay-close::before {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg); }
  .overlay-close::after {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg); }

/* Effects */
.overlay-door {
  visibility: hidden;
  width: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  -webkit-transition: width 0.5s 0.3s, visibility 0s 0.8s;
  transition: width 0.5s 0.3s, visibility 0s 0.8s; }
  .overlay-door.open {
    visibility: visible;
    width: 100%;
    -webkit-transition: width 0.5s;
    transition: width 0.5s; }
  .overlay-door.open .overlay-description, .overlay-door.open .overlay-title, .overlay-door.open .overlay-imagecontainer {
    opacity: 1;
    transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -webkit-transition: opacity 1s ease-in-out; }
  .overlay-door.open .overlay-title-effect {
    color: #000;
    animation: 1.5s 0.7s float infinite ease;
    height: auto;
    min-height: 40px;
    display: block; }
    .overlay-door.open .overlay-title-effect::after {
      width: 100%;
      opacity: 1;
      transition-delay: 1.5s;
      -webkit-transition-delay: 1.5s; }
  .overlay-door nav {
    position: absolute;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
    transform: translateY(-50%) translateX(-50%);
    opacity: 0;
    -webkit-transition: opacity 0.3s 0.5s;
    transition: opacity 0.3s 0.5s; }
  .overlay-door .overlay-close {
    opacity: 0;
    -webkit-transition: opacity 0.3s 0.5s;
    transition: opacity 0.3s 0.5s; }
  .overlay-door.open nav, .overlay-door.open .overlay-close {
    opacity: 1;
    -webkit-transition-delay: 0.5s;
    transition-delay: 0.5s; }
  .overlay-door.close nav, .overlay-door.close .overlay-close {
    -webkit-transition-delay: 0s;
    transition-delay: 0s; }

