.hexagon-wrapper {
  text-align: center;
  margin: 20px;
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px; }

.hexagon {
  background-color: #E3B673;
  border: 1px solid #000000;
  height: 100%;
  width: calc(100% * 0.57735);
  display: inline-block; }
  .hexagon:before {
    position: absolute;
    top: 0;
    right: calc((100% / 2) - ((100% * 0.57735) / 2));
    background-color: inherit;
    height: inherit;
    width: inherit;
    content: '';
    transform: rotateZ(60deg);
    z-index: 3;
    border-top: 1px solid black;
    border-bottom: 1px solid #000000; }
  .hexagon:after {
    position: absolute;
    top: 0;
    right: calc((100% / 2) - ((100% * 0.57735) / 2));
    background-color: inherit;
    height: inherit;
    width: inherit;
    content: '';
    transform: rotateZ(-60deg);
    z-index: 3;
    border-top: 1px solid black;
    border-bottom: 1px solid #000000; }

.hexagon-icon-position {
  color: #000;
  position: relative;
  z-index: 4;
  top: 12px;
  -webkit-transition: -webkid-transform .25s ease;
  -moz-transition: -moz-transform .25s ease;
  -ms-transition: -ms-transform .25s ease;
  -o-transition: -o-transform .25s ease;
  transition: transform .25s ease; }

.hexagon-hover-rotate {
  -webkit-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  -o-transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition: -webkid-transform .25s ease;
  -moz-transition: -moz-transform .25s ease;
  -ms-transition: -ms-transform .25s ease;
  -o-transition: -o-transform .25s ease;
  transition: transform .25s ease; }

.ripple-effect {
  position: absolute;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background: #cccccc;
  animation: ripple-animation 2s; }

@keyframes ripple-animation {
  from {
    transform: scale(1);
    opacity: 0.8; }
  to {
    transform: scale(100);
    opacity: 0; } }

.spot {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.svg-wrapper {
  margin-top: 40px;
  position: relative;
  width: 150px;
  /*make sure to use same height/width as in the html*/
  height: 40px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
  overflow: hidden;
  cursor: pointer; }

.shape {
  display: block;
  stroke-width: 4px;
  fill: transparent;
  stroke: #ccc;
  stroke-dasharray: 85 400;
  stroke-dashoffset: -220;
  transition: 1.5s all ease; }

.text {
  display: block;
  margin-top: -35px;
  text-align: center; }
  .text a {
    color: #454648;
    text-decoration: none;
    text-rendering: optimizeLegibility;
    font-family: 'Roboto', Arial, sans-serif;
    font-size: 14px;
    line-height: 1; }

.svg-wrapper:hover .shape {
  stroke-dasharray: 50 0;
  stroke-width: 3px;
  stroke-dashoffset: 0;
  stroke: #E3B673; }

.active .shape {
  stroke-dasharray: 50 0;
  stroke-width: 3px;
  stroke-dashoffset: 0;
  stroke: #FFEFFF; }

@font-face {
  font-family: 'pe-icon-social';
  src: url("./fonts/pe-icon-social.eot");
  src: url("./fonts/pe-icon-social.eot") format("embedded-opentype"), url("./fonts/pe-icon-social.woff") format("woff"), url("./fonts/pe-icon-social.ttf") format("truetype"), url("./fonts/pe-icon-social.svg") format("svg");
  font-weight: normal;
