.button {
  display: inline-block;
  padding: 23px 63px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out; }
  .button.main {
    background: #7b77c9;
    color: #E3B673;
    font-family: 'Roboto', Arial, sans-serif;
    font-size: 14px; }
  .button.white {
    background: #E3B673;
